import { prisma } from "@/lib/prisma";
export const dynamic = "force-dynamic";

export default async function ProductsPage() {
  const products = await prisma.product.findMany({
    orderBy: { createdAt: "desc" },
    include: { ArtisanProfile: { select: { slug: true, displayName: true } } },
  });

  return (
    <main className="mx-auto max-w-7xl p-6 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
      {products.map(p => (
        <a key={p.id} href={`/products/${p.id}`} className="border rounded p-3">
          <img src={p.imageUrl || "/placeholder.jpg"} alt={p.name} className="w-full h-56 object-cover rounded" />
          <div className="mt-2 font-semibold">{p.name}</div>
          <div className="text-slate-600">â‚±{(p.priceCents / 100).toFixed(2)}</div>
          {p.ArtisanProfile && (
            <div className="text-slate-500 text-sm">by {p.ArtisanProfile.displayName}</div>
          )}
        </a>
      ))}
      {products.length === 0 && <p className="text-slate-500">No products yet.</p>}
    </main>
  );
}